import React from "react";
import { Redirect } from "react-router-dom";

const Home = React.lazy(() => import("@/pages/home"));
const Mine = React.lazy(() => import("@/pages/mine"));

const routes = [
  {
    path: "/",
    exact: true,
    render: () => <Redirect to="/home" />,
  },
  {
    path: "/home",
    component: Home,
  },
  {
    path: "/mine",
    component: Mine,
  },
];

export default routes;
